  data () {
    return {
      heroContent: {
        title: '國際服務 服務台灣',
        subTitle: '開展台灣的國際人道服務，開拓台灣的國際發展空間',
        btnUrl: 'https://camtw.eoffering.org.tw/',
        imageNo: 1
      }
    }
  }
}
</script>
<style lang="sass">
.info
  +fs($lh: 1.6)
  padding: 20px
</style>



