// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueScrollReveal from 'vue-scroll-reveal'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faBars, faTimesCircle, faFileInvoiceDollar, faFileAlt, faDonate, faExternalLinkAlt, faLanguage, faDesktop, faUser, faCommentDots, faArrowUp } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import VueModalTor from 'vue-modaltor'
import vueSmoothScroll from 'vue-smooth-scroll'

Vue.config.productionTip = false
Vue.use(VueScrollReveal, {
  class: 'v-scroll-reveal',
  reset: false
})
Vue.use(VueAwesomeSwiper)
Vue.use(VueModalTor)
Vue.use(vueSmoothScroll)

library.add(faBars, faTimesCircle, faFileInvoiceDollar, faFileAlt, faDonate, faExternalLinkAlt, faLanguage, faDesktop, faUser, faCommentDots, faArrowUp)

Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



